<template>
  <div>
    <canvas ref="chartCanvas" width="1050" height="350"></canvas>
  </div>
</template>

<script>
import {
  Chart,
  ScatterController,
  TimeScale,
  LinearScale,
  PointElement,
  LineController,
  LineElement,
} from "chart.js";
import "chartjs-adapter-date-fns";

Chart.register(
  ScatterController,
  TimeScale,
  LinearScale,
  PointElement,
  LineController,
  LineElement
);

export default {
  mounted() {
    this.fetchBitcoinData();
  },
  methods: {
    async fetchBitcoinData() {
      try {
        const response = await fetch("http://18.133.225.81:3000/api/v1/read");
        const data = await response.json();
        const prices = data.map((item) => ({
          x: new Date(item.timestamp),
          y: item.price,
        }));

        const ctx = this.$refs.chartCanvas.getContext("2d");

        new Chart(ctx, {
          type: "line",
          data: {
            datasets: [
              {
                label: [
                  "January",
                  "February",
                  "March",
                  "April",
                  "May",
                  "June",
                  "July",
                ],
                data: prices,
                backgroundColor: "rgba(75, 192, 192, 0.2)",
                borderColor: "rgba(75, 192, 192, 1)",
                borderWidth: 1,
                fill: "start",
              },
            ],
          },
          options: {
            responsive: true,
            scales: {
              x: {
                type: "time",
                time: {
                  unit: "hour",
                },
              },
              y: {
                beginAtZero: true,
              },
            },
          },
        });
      } catch (error) {
        console.error(error);
      }
    },
  },
};
</script>
